*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.contacts-btn, .socials__link {
  justify-content: center;
  align-items: center;
  display: flex;
}

:root {
  --main-text-color: #000;
  --secondary-text-color: #fff;
  --complementary-text-color: #907e82;
  --secondary-title-color: #ffa5ba;
  --additional-text-color: #c7133e;
  --accent-color: #d41443;
  --main-bg-color: #fff;
  --secondary-bg-color: #fff5f6;
  --complementary-bg-color: #ffb8ca;
  --additional-bg-color: #fafafa;
  --mob-menu-bg-color: #e17992;
  --icons-bg-color: #ffa5bb;
  --main-btn-hover-bg-color: #ecebeb;
  --secondary-btn-hover-bg-color: #d41342;
  --additional-bg-color-cream: #ffb8ca;
  --additional-bg-color-coffee: #f0d1a5;
  --additional-bg-color-shake: #c2e297;
  --border-mobile-menu: #e18298;
  --timing-transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: DM Sans;
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.button {
  vertical-align: middle;
  height: 44px;
  color: var(--accent-color);
  background-color: var(--main-bg-color);
  cursor: pointer;
  transition: background-color var(--timing-transition);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 22px;
  align-items: center;
  padding: 0;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
  display: inline-block;
}

.button:hover, .button:focus {
  background-color: var(--main-btn-hover-bg-color);
}

.button__icon-send {
  vertical-align: middle;
  align-items: center;
  margin-left: 12px;
  display: inline-block;
}

.button--header {
  width: 209px;
}

@media (min-width: 768px) {
  .button--header {
    width: 186px;
  }
}

@media (min-width: 1200px) {
  .button--header {
    width: 159px;
  }
}

.button--hero {
  width: 83px;
  height: 24px;
  font-size: 16px;
  font-weight: 700;
}

@media (min-width: 1200px) {
  .button--hero {
    width: 138px;
    height: 40px;
  }
}

.button--pink {
  height: 24px;
  width: 62px;
  color: var(--main-bg-color);
  background-color: var(--accent-color);
  transition: var(--timing-transition);
}

@media (min-width: 1200px) {
  .button--pink {
    width: 104px;
  }
}

.button--pink:hover, .button--pink:focus {
  background-color: var(--accent-color);
}

.button--about {
  width: 176px;
}

.button--white {
  width: 161px;
}

@media (min-width: 768px) {
  .button--white {
    width: 174px;
  }
}

@media (min-width: 1200px) {
  .button--white {
    width: 168px;
  }
}

.button--colored {
  vertical-align: middle;
  height: 44px;
  color: var(--main-bg-color);
  background-color: var(--accent-color);
  cursor: pointer;
  width: 161px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 22px;
  align-items: center;
  padding: 0;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
  display: inline-block;
}

@media (min-width: 768px) {
  .button--colored {
    width: 192px;
  }
}

@media (min-width: 1200px) {
  .button--colored {
    width: 198px;
  }
}

.mobile-menu {
  width: 250px;
  height: 502px;
  background-color: var(--mob-menu-bg-color);
  transition: var(--timing-transition);
  padding: 20px;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

@media (min-width: 480px) {
  .mobile-menu {
    transition: var(--timing-transition);
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    width: 254px;
    height: 100vh;
    transition: var(--timing-transition);
  }
}

@media (min-width: 1200px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu__nav-link {
  color: var(--secondary-text-color);
  border-top: 1px solid var(--border-mobile-menu);
  align-items: center;
  padding-top: 11px;
  padding-bottom: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  display: block;
}

.mobile-menu__button {
  width: 209px;
  height: 44px;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 60px;
  margin-bottom: 32px;
  display: flex;
}

.mobile-menu.is-open {
  z-index: 10;
  transition: var(--timing-transition);
  transform: translateX(0);
}

.btn-mobile-menu {
  fill: var(--accent-color);
  z-index: 100;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-items: center;
  margin-left: auto;
  display: block;
}

@media (min-width: 1200px) {
  .btn-mobile-menu {
    display: none;
  }
}

.btn-mobile-menu.is-open {
  transform: translate(200px, -30px);
}

.btn-mobile-menu__burger {
  fill: var(--accent-color);
  width: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-top: 27px;
  margin-left: auto;
  padding: 0;
}

.btn-mobile-menu.is-open .btn-mobile-menu__burger {
  opacity: 0;
  transform: translate(200px, -30px);
}

@media (min-width: 1200px) {
  .btn-mobile-menu__burger {
    display: none;
  }
}

.btn-mobile-menu__close {
  opacity: 0;
  fill: var(--secondary-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-top: 20px;
  margin-bottom: 26px;
  margin-left: auto;
  padding: 0;
}

.btn-mobile-menu.is-open .btn-mobile-menu__close {
  opacity: 1;
}

@media (min-width: 1200px) {
  .btn-mobile-menu__close {
    display: none;
  }
}

.mobile-menu__nav-link:last-child {
  border-top: 1px solid var(--border-mobile-menu);
}

.current {
  color: var(--accent-color);
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transition: opacity var(--timing-transitio), visibility var(--timing-transitio);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: var(--complementary-bg-color);
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media (min-width: 768px) {
  .modal {
    width: 450px;
    height: 609px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 581px;
  }
}

.modal__button {
  width: 30px;
  height: 30px;
  background-color: var(--main-bg-color);
  cursor: pointer;
  transition: color var(--timing-transition);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__button:hover > .modal__icon--clos, .modal__button:focus > .modal__icon--clos {
  fill: var(--accent-color);
}

.modal__menu {
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

.order-form {
