  }
}

@media (min-width: 1200px) {
  .contact-form--franch {
    width: 420px;
  }
}

.contact-form__label {
  color: var(--accent-color);
  margin-bottom: 4px;
  font-size: 14px;
  line-height: 18px;
}

.contact-form__group {
  width: 100%;
