}

.feedback__form {
  height: 120px;
  transition: border-color var(--timing-transition);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding-top: 12px;
  padding-left: 16px;
}

.feedback__form::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.feedback__form::placeholder {
  letter-spacing: .01em;
